*{
  margin:0;
  padding:0;
}

ul li{
  list-style: none;
}
.App-wrapper {
  width: 100%;
  height: 100vh;
  background: #ddeaec;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  width: 500px;
  background: white;
  text-align: center;
  min-height: 600px;
  box-sizing: border-box;
  box-shadow: 0px 0px 10px -7px black;
  border-radius: 1px;
  
}
.App h1 {
  font-size: 21px;
  font-weight: bold;
  background: #178596;
  width: 76%;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 8px 3px;
  border-radius: 5px;
  color: #ffffff;
  font-family: cursive;
}

.App .Messege-box {
  border: 1px solid #ddeaec;
  width: 74%;
  margin: 0 auto;
  height: 387px;
  padding: 5px;
  text-align: initial;
  max-height: 387px;
  overflow: scroll;
}
.App .Messege-box::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.Messege-item.UsrMessege {
  width: fit-content;
  margin-left: auto;
}
span.Sender {
  color: #909090;
  font-size: 12px;
  text-transform: capitalize;
}
p.Messege {
  font-family: cursive;
  background: #ddeaec;
  width: fit-content;
  padding: 10px;
  border-radius: 5px;
}
.App .Send-box {
  width: 76%;
  margin: 0 auto;
  position: relative;
}
.App input {
  border: 1px solid #abc9cea8;
  border-radius: 3px;
  height: 32px;
  box-shadow: 0px 0px 7px -2px #ddeaec;
  padding: 6px 1px;
  width: 100%;
  outline: none;
  font-size: 18px;
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
  margin-top: 14px;
  color: #000000;
}
.App button {
  padding: 4px 10px;
  height: 46px;
  font-size: 19px;
  font-weight: 500;
  font-family: auto;
  outline: none;
  border: 1px solid #ddeaec;
  background: #178596;
  margin-left: 7px;
  color: #fff;
  border-radius: 2px;
  cursor: pointer;
  position: absolute;
  top: 24%;
  right: -1%;
  width: 63px;
}
.app .todo-lists {
  width: 70%;
  margin: 0 auto;
  margin-top: 20px;
}